import { useState, useEffect, useRef } from "react";
import { SlideModal } from "../../atoms/modalAtom";
import { useSetRecoilState, useRecoilValue } from "recoil";
import styled from "styled-components";

// 모달 오버레이
const ModalOverlay = styled.div`
  position: fixed;
  background-color: rgba(0, 0, 0, 0);
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-y: initial;
  z-index: 101;
  transition: background-color 0.3s ease-in-out;
  &.open {
    background-color: rgba(0, 0, 0, 0.6);
  }
`;
// 모달 컨테이너 div
const ModalContainer = styled.div`
  position: fixed;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  scrollbar-width: none;
  background-color: #fff;
  transition: top 0.3s ease-in-out;
  padding: 0 15px;
  &.open {
    top: 0;
  }
`;
// 모달 헤더
const ModalHeader = styled.div`
  display: flex;
  height: 50px;
  align-items: center;
`;
// 모달 닫기
const ModalCloseBtn = styled.button`
  border: 0;
  background: none;
  padding: 5px 0;
`;

const RoomModal = ({ children }) => {
  const setSlideModal = useSetRecoilState(SlideModal);
  const slideModal = useRecoilValue(SlideModal);
  const [isSlideUp, setIsSlideUp] = useState(false);
  const timer = useRef();

  // 모달 슬라이드 업 애니메이션 0.1초 후 실행
  useEffect(() => {
    if (slideModal) {
      timer.current = setTimeout(() => setIsSlideUp(true), 100);
    } else {
      clearTimeout(timer.current);
    }
  }, [slideModal]);

  const onCloseModal = () => {
    setIsSlideUp(false);
    setTimeout(() => {
      setSlideModal(false);
    }, 200);
  };

  return (
    <ModalOverlay className={isSlideUp ? "open" : null}>
      <ModalContainer className={isSlideUp ? "open" : null}>
        <ModalHeader>
          <ModalCloseBtn onClick={onCloseModal}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              xmlnsXlink="http://www.w3.org/1999/xlink"
              width="24"
              height="24"
              viewBox="0 0 20 20"
              fill="none"
            >
              <rect width="20" height="20" fill="url(#pattern0_2_619)" />
              <defs>
                <pattern
                  id="pattern0_2_619"
                  patternContentUnits="objectBoundingBox"
                  width="1"
                  height="1"
                >
                  <use xlinkHref="#image0_2_619" transform="scale(0.0104167)" />
                </pattern>
                <image
                  id="image0_2_619"
                  width="96"
                  height="96"
                  xlinkHref="data:image/png;base64,"
                />
              </defs>
            </svg>
          </ModalCloseBtn>
        </ModalHeader>
        {children}
      </ModalContainer>
    </ModalOverlay>
  );
};

export default RoomModal;
